import React from "react";
import { Route, Routes } from "react-router-dom";
import GooglePlayPage from "./GooglePlayPage";
import IfoodPage from "./IfoodPage";
import NetflixPage from "./NetflixPage";
import UbereatsPage from "./UbereatsPage";
import UberPage from "./UberPage";

const GiftRouter = () => {
  return (
    <Routes>
      <Route path="ifood" element={<IfoodPage />} />
      <Route path="uber-eats" element={<UbereatsPage />} />
      <Route path="uber" element={<UberPage />} />
      <Route path="GooglePlay" element={<GooglePlayPage />} />
      <Route path="Netflix" element={<NetflixPage />} />
    </Routes>
  );
};

export default GiftRouter;
