import React from "react";
import Styles from "./ModalCart.module.css";
import Button from "../../forms/Button";
import { Link } from "react-router-dom";

const ModalCart = ({ setModal }) => {
  function closeModal(e) {
    if (e.target.id === "modal") setModal(false);
  }

  function continueComprenado() {
    setModal(false);
  }

  

  return (
    <div className={Styles.modal} id="modal" onClick={closeModal}>
      <div className={Styles.card}>
        <div>Item adicionado com sucesso!</div>
        <Link to="/carrinho">
          <Button cor="roxo">
            Ir para o carrinho
          </Button>
        </Link>
        <Button cor="roxo" onClick={continueComprenado}>
          Continuar Comprando
        </Button>
      </div>
    </div>
  );
};

export default ModalCart;
