import React from "react";
import { GlobalContext } from "../../../GloblStorage";
import MonetSvg from "../../svgs/MonetSvg";
import Styles from "./Money.module.css";

const Money = ({ setActive }) => {
  const [money, setMoney] = React.useState(null);

  const Global = React.useContext(GlobalContext);

  function refreshMoney() {
    let userI = localStorage.getItem("userI");
    if(userI) setMoney(JSON.parse(userI).money);
  }

  React.useEffect(() => {
    refreshMoney();
  }, [Global, money]);

  return (
    <div
      className={Styles.container}
      onClick={() => {
        setActive((u) => !u);
      }}
    >
      <MonetSvg />
      <div>
        {money !== undefined &&  money !== null ? money.toFixed(1).toString().replace(".", ",") : "Error"}
      </div>
    </div>
  );
};

export default Money;
