import React from "react";
import Styles from "./LaraPage.module.css";
import Loading from "../../../../Partials/Loading";
import InfoLara from "./Components/InfoLara";
import { GlobalContext } from "../../../../../../GloblStorage";

const LaraPage = () => {
  const [data, setData] = React.useState([]);

  const Global = React.useContext(GlobalContext);

  async function getDatas() {
    const response = await fetch(Global.url + "/prods/lara", {
      method: "POST",
      headers: {
        "Content-Type": "application/json",
      },
    });
    const json = await response.json();
    setData(json);
    return json;
  }

  React.useEffect(() => {
    getDatas();
  }, []);

  return (
    <div className={Styles.container}>
      {data ? (
        data.length > 0 ? (
          <>
            <div className={Styles.titles}>
              <div>Valor</div>
              <div>Vendedor</div>
              <div>Titular</div>
              <div>Banco</div>
              <div>PIX</div>
              <div>CPF</div>
              <div>Nome</div>
              <div>Nivel</div>
            </div>
            {data.map((card, index) => (
              <InfoLara key={card.titular + index} {...card} />
            ))}
          </>
        ) : (
          <div className={Styles.err}>No h itens disponiveis</div>
        )
      ) : (
        <Loading />
      )}
    </div>
  );
};
export default LaraPage;
