import React from "react";
import { GlobalContext } from "../../../../../../../GloblStorage";
import Car from "../../../../../../svgs/Car";
import ModalCart from "../../../../../Partials/ModalCart";
import Styles from "./InfoConsul.module.css";
import cardItau from "../../../../../../../assets/cartaos/CartaoItau.png";
import cardMagazine from "../../../../../../../assets/cartaos/CartaoMagazine.png";
import cardHyper from "../../../../../../../assets/cartaos/CartaoHyper.png";
import cardCredcard from "../../../../../../../assets/cartaos/CartaoCredcard.png";
import cardSantander from "../../../../../../../assets/cartaos/CartaoSantander.png";

const InfoConsul = ({ value, limite, id, loja, bin }) => {
  const [modal, setModal] = React.useState(false);
  const Global = React.useContext(GlobalContext);

  function handleBuy() {
    setModal(true);
    fetch(Global.url + "/postCart", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify({
        user: Global.userInfos.user,
        prod: {
          prod: "consultavel",
          loja,
          newProd: {
            type: `Consultavel - ${loja}`,
            id,
            value: Number(value),
            quantidade: 1,
            loja,
          },
        },
      }),
    });
  }

  return (
    <>
      {modal && <ModalCart setModal={setModal} />}
      <div className={Styles.card}>
        {loja === "itau" && <img src={cardItau} alt="" />}
        {loja === "magazine" && <img src={cardMagazine} alt="" />}
        {loja === "hipercard" && <img src={cardHyper} alt="" />}
        {loja === "credicard" && <img src={cardCredcard} alt="" />}
        {loja === "santander" && <img src={cardSantander} alt="" />}
        <div className={Styles.infos}>
          <div className={Styles.preco}>R${value.toFixed(2)}</div>
          <div>
            {bin.split("").slice(0, 4)} {bin.split("").slice(4)}XX XXXX XXXX
          </div>
          <div className={Styles.infosBtn}>
            <div className={Styles.limite}>LIMITE: {limite}</div>
            <div className={Styles.btnBuy} onClick={handleBuy}>
              <Car />
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default InfoConsul;
