import React from "react";
import { Route, Routes } from "react-router";
import { Link } from "react-router-dom";
import { GlobalContext } from "../../../../../GloblStorage";
import ConfigPage from "./pages/config/ConfigPage";
import ExtratoPage from "./pages/extrato/ExtratoPage";
import Styles from "./Perfil.module.css";

const Perfil = () => {
  const Global = React.useContext(GlobalContext);

  const [active, setActive] = React.useState(
    Global.pathnameState.split("/").at(-1),
  );

  React.useEffect(() => {
    setActive(Global.pathnameState.split("/").at(-1));
  }, [Global.pathnameState]);

  return (
    <main className={Styles.container}>
      <nav className={Styles.nav}>
        <ul>
          <li className={active === "" ? Styles.select : null}>
            <Link
              to="/minha-conta/"
              onClick={() => {
                setTimeout(() => {
                  Global.setPathName();
                }, 10);
              }}
            >
              Configuraes
            </Link>
          </li>
          <li className={active === "extrato" ? Styles.select : null}>
            <Link
              to="/minha-conta/extrato"
              onClick={() => {
                setTimeout(() => {
                  Global.setPathName();
                }, 10);
              }}
            >
              {" "}
              Extrato
            </Link>
          </li>
        </ul>
      </nav>
      <section>
        <Routes>
          <Route path="" element={<ConfigPage />} />
          <Route path="extrato" element={<ExtratoPage />} />
        </Routes>
      </section>
    </main>
  );
};

export default Perfil;
