import React from 'react';
import io from 'socket.io-client';

const url = '';
let ioClient = io(url);

export const GlobalContext = React.createContext();

export const GloblStorage = ({ children }) => {
  const [token, setToken] = React.useState(null);
  const [nots, setNots] = React.useState([]);
  const [userInfos, setUserInfos] = React.useState({});
  const [pathnameState, setPathnameState] = React.useState(
    window.location.pathname
  );

  async function refreshDatas() {
    let token = localStorage.getItem('token');
    setToken(localStorage.getItem('token'));

    try {
      const res = await fetch(url + '/getDatasByToken', {
        method: 'POST',
        headers: {
          'content-type': 'application/json'
        },
        body: JSON.stringify({ token })
      });
      if (res.status !== 200) {
        localStorage.removeItem('userI');
        localStorage.removeItem('token');
        localStorage.removeItem('remember');
        localStorage.removeItem('0');
        localStorage.removeItem('loginCont');
      } else {
        const json = await res.json();
        localStorage.setItem('userI', JSON.stringify(json));
        setUserInfos(json);
      }
    } catch {}
  }

  React.useEffect(() => {
    if (localStorage.getItem('token')) refreshDatas();
  }, []);

  function newNotification(msg, type) {
    let id = Math.random();
    setNots([...nots, { id, msg, type }]);
    setTimeout(() => {
      const newNots = nots.filter((n) => n.id !== id);
      setNots(newNots);
    }, 1500);
  }

  function updateUserInfos(user) {
    setUserInfos(user);
  }

  function setPathName() {
    setPathnameState(window.location.pathname);
  }

  return (
    <GlobalContext.Provider
      value={{
        token,
        userInfos,
        updateUserInfos,
        nots,
        newNotification,
        url,
        setPathName,
        pathnameState,
        io: ioClient
      }}
    >
      {children}
    </GlobalContext.Provider>
  );
};
