import React from "react";
import Styles from "./AddMoney.module.css";
import Button from "../../forms/Button";
import { GlobalContext } from "../../../GloblStorage";
import Copy from "../../svgs/Copy";
import CircularSlider from "@fseehawer/react-circular-slider";

const AddMoney = ({ active, setActive }) => {
  const [value, setValue] = React.useState(20);
  const [inputValue, setInputValue] = React.useState(20);
  const [loading, setLoading] = React.useState(false);
  const [pixGerado, setPixGerado] = React.useState(false);
  const [pixPago, setPixPago] = React.useState(false);
  const [copy, setCopy] = React.useState(false);
  const [array, setArray] = React.useState([]);

  const checkPayRef = React.useRef()
  const Global = React.useContext(GlobalContext);

  Global.io.on("payPix", (res) => {
    setPixPago(res.paga)
    
  })

  React.useEffect(() => {
    let arr = [];
    for (let i = 20; i <= 1000; i += 10) {
      arr.push(i);
    }
    setArray(arr);
  }, [value]);

  function handleClick() {
    if (!value)
      return Global.newNotification("Digite um Valor Valido", "alert");

    async function gerarPix() {
      setLoading(true);
      const response = await fetch(Global.url + "/pix/gerar", {
        method: "POST",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify({
          value,
          user: Global.userInfos.user,
        }),
      });
      let jsonRes = await response.json();
      if (jsonRes) {
        checkPayRef.current = setInterval(() => {
          Global.io.emit("checkCob", { txid: jsonRes.txid })
        }, 5000)
        setPixGerado(jsonRes)
      };
      setLoading(false);
    }

    gerarPix();
  }

  function onChange(e) {
    let val = +e.target.value;
    let lastNumber = e.target.value.at(-1);

    setInputValue(e.target.value);

    if (lastNumber >= 5) {
      let soma = 10 - lastNumber;
      val += soma;
    } else {
      val -= lastNumber;
    }

    if (!val) setValue(20);
    else if (val < 20) setValue(20);
    else if (val > 1000) setValue(1000);
    else setValue(val);
  }

  function CopyPix() {
    document.querySelector("#copiaeColaId").select();
    document.execCommand("copy");
    setCopy(true);
  }

  function DisablePix() {
    setPixGerado(null);
  }

  if (!active) return null;
  return (
    <section className={Styles.container}>
      <div className={Styles.content}>
        <div className={Styles.title}>
          <h3>Loja de creditos</h3>
          <span
            onClick={() => {
              setActive(false);
            }}
          >
            +
          </span>
        </div>
        {!pixGerado && (
          <div>
            <link
              rel="stylesheet"
              href="https://cdnjs.cloudflare.com/ajax/libs/roundSlider/1.3.2/roundslider.css"
            />

            <div className={Styles.buy}>
              <div className={Styles.roletaContainer}>
                <CircularSlider
                  label="Creditos"
                  labelColor="#e14eca"
                  knobColor="#2c06ff"
                  progressColorFrom="#ff47e3"
                  progressColorTo="#e14eca"
                  progressSize={24}
                  trackColor="#eeeeee"
                  trackSize={24}
                  data={array}
                  dataIndex={array.indexOf(value)}
                  onChange={(value) => {
                    setValue(value);
                    setInputValue(value);
                  }}
                />
              </div>
              <div className={Styles.containerInfos}>
                <h2>Resumo</h2>
                <div className={Styles.creditos}>
                  Credito
                  <input type="number" value={inputValue} onChange={onChange} />
                </div>
                <div className={Styles.valor}>
                  Valor
                  <span>R${value}</span>
                </div>
              </div>
            </div>
            <Button  loading={loading}>
              Pagar com pix
            </Button>
          </div>
        )}
        {pixGerado && (
          <div className={Styles.containerBuy}>
            <div className={Styles.containerPix}>
              <div className={Styles.pixCopiaCola}>
                <span>PIX COPIA E COLA</span>
                <div
                  className={Styles.copiaColaContainer}
                  style={{
                    border: `1px solid ${copy ? "#2c06ff" : "#1e0996"}`,
                  }}
                >
                  <input
                    type="text"
                    id="copiaeColaId"
                    value={pixGerado.copiaCola}
                    className={Styles.copiaCola}
                  />
                  <div
                    className={Styles.btnCopy}
                    onClick={CopyPix}
                    style={{ backgroundColor: copy ? "#2c06ff" : "#1e0996" }}
                  >
                    <Copy />
                  </div>
                </div>
              </div>
              <div className={Styles.pixQrCode}>
                <span>PIX QRCODE</span>
                <img src={pixGerado.qrcode} alt="qrcode pix" />
                <span>R$ {value}</span>
              </div>
            </div>
            <div className={`${Styles.status} ${pixPago && Styles.pago}`}>
              {!pixPago ? "Pagamento pendendte" : "Pagamento Concluido" }
            </div>
            <Button onClick={DisablePix}>Gerar Novo Pix</Button>
          </div>
        )}
      </div>
    </section>
  );
};

export default AddMoney;
