import React from 'react';
import Styles from './LoginLojaInfo.module.css';
import Car from '../../../../../../svgs/Car';
import { GlobalContext } from '../../../../../../../GloblStorage';
import ModalCart from '../../../../../Partials/ModalCart';

const LoginLojaInfo = ({ loja, preco, estoque }) => {
  const [modal, setModal] = React.useState(false);

  const Global = React.useContext(GlobalContext);

  async function handleBuy() {
    setModal(true);
    fetch(Global.url + '/postCart', {
      method: 'POST',
      headers: {
        'content-type': 'application/json'
      },
      body: JSON.stringify({
        user: Global.userInfos.user,
        prod: {
          prod: 'login',
          newProd: {
            itemProd: 'login',
            type: `Login - ${loja}`,
            loja,
            value: Number(preco),
            quantidade: 1
          }
        }
      })
    });
  }

  if (estoque === 0) return null;

  return (
    <>
      {modal && <ModalCart setModal={setModal} />}

      <div className={`${Styles.item} ${!estoque && Styles.noStock}`}>
        <div className={Styles.btn} onClick={handleBuy}>
          <Car />
        </div>
        <div>{loja}</div>
        <div>R${preco.toFixed(2)}</div>
      </div>
    </>
  );
};

export default LoginLojaInfo;
