import React from "react";
import Styles from "./CcCard.module.css";
import InfoCard from "./Components/InfoCard";
import Loading from "../../../../Partials/Loading";
import { GlobalContext } from "../../../../../../GloblStorage";

const CcCardFull = () => {
  const [pages, setPages] = React.useState(null);
  const [pageActive, setPageActive] = React.useState(0);
  
  const Global = React.useContext(GlobalContext);

  const [search, setSearch] = React.useState('')
  const [data, setData] = React.useState([])

  function Search(e) {
    setSearch(e.target.value);
    if(data.length < 1) return setPages([[]])
    let arr = [];
    let arrSearchs = data.filter(c => {
      let bin = c.bin.toString()
      return bin.includes(e.target.value)
    });
    if(arrSearchs.length < 1) return setPages([[]])
    let countPagesNum = (Math.ceil(arrSearchs.length / 30))
    for (let i = 0; i < countPagesNum; i++){
       arr.push(arrSearchs.slice(i * 30, (i + 1) * 30))
    }
    setPages(arr)
  }

  React.useState(async () => {
    async function getDatas() {
      const response = await fetch(Global.url + "/prods/card", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ type: "ccfull" }),
      });
      const json = await response.json();
      setData(json)
      if(json.length < 1) return setPages([[]])
      let arr = [];
      let countPagesNum = (Math.ceil(json.length / 30))
      for (let i = 0; i < countPagesNum; i++){
        arr.push(json.slice(i * 30, (i + 1) * 30))
      }
      setPages(arr)
      if(json.length < 1) setPages([[]])
      return json;
    }
    getDatas()
  }, []);

  return (
    <>
      <div className={Styles.search}>
        <input onChange={Search} value={search} type="text" placeholder="Pesquisar por BIN" />
      </div>
      <div className={Styles.container}>
      {pages ? (
        pages[0].length > 0 ? (
          <>
            <div className={Styles.titles} id={Styles.titles}>
              <div>Valor</div>
              <div>Vendedor</div>
              <div>BIN</div>
              <div>Validade</div>
              <div>Bandeira</div>
              <div>Pais</div>
              <div>Tipo</div>
              <div>Nivel</div>
              <div>Emissor</div>
              <div>CPF</div>
              <div>Nome</div>
            </div>
            {pages[pageActive].map((card) => (
              <InfoCard
              {...card}
              key={card.id}
              type="Cc Full"
              itemProd="ccfull"
              />
              ))}
            <div className={Styles.nav}>              
              {pages.length > 1 && pages.map((list, index) =>
                <a href={"#" + Styles.titles}>
                  <div className={index === pageActive && Styles.active} onClick={() => setPageActive(index)}>
                    {index + 1}
                  </div>
                </a>)}
            </div>
          </>
        ) : (
          <div className={Styles.err}>No h itens disponiveis</div>
          )
          ) : (
            <Loading />
            )}
    </div>
    </>
  );
};
export default CcCardFull;
