import React from "react";
import Input from "../../../../../../forms/Input";
import Button from "../../../../../../forms/Button";
import Styles from "./ConfigPage.module.css";
import { GlobalContext } from "../../../../../../../GloblStorage";

const ConfigPage = () => {
  const [senhaAntiga, setSenhaAntiga] = React.useState("");
  const [senhaNova, setSenhaNova] = React.useState("");
  const [repeatSenhaNova, setRepeatSenhaNova] = React.useState("");

  const Global = React.useContext(GlobalContext);

  async function changePass(e) {
    e.preventDefault()
    if (senhaNova !== repeatSenhaNova)
      return Global.newNotification("As Senhas No Conferem", "alert");
    if (senhaNova.length < 6)
      return Global.newNotification("Senha Muito Curta", "alert");
    const token = localStorage.getItem("token");
    const res = await (
      await fetch(Global.url + "/changePassword", {
        method: "POST",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify({
          oldPass: senhaAntiga,
          newPass: senhaNova,
          token,
        }),
      })
    ).json();

    if (res.error) Global.newNotification(res.message, "alert");
    if (!res.error) Global.newNotification(res.message);

    setRepeatSenhaNova('')
    setSenhaAntiga('')
    setSenhaNova('')
  }

  return (
    <div className={Styles.container}>
      <div className={Styles.title}>Configuraes</div>
      <div className={Styles.containerAlterar}>
        <div className={Styles.subTitle}>Alterar Senha</div>
        <form onSubmit={changePass}>
          <Input
            type="password"
            name="Senha antiga"
            icon="lock"
            value={senhaAntiga}
            setValue={setSenhaAntiga}
            required={true}
          />
          <Input
            type="password"
            name="Nova Senha"
            icon="lock"
            value={senhaNova}
            setValue={setSenhaNova}
            required={true}
          />
          <Input
            type="password"
            name="Nova Senha Novamente"
            icon="lock"
            value={repeatSenhaNova}
            setValue={setRepeatSenhaNova}
            required={true}
          />
          <Button>Alterar Senha</Button>
        </form>
      </div>
    </div>
  );
};

export default ConfigPage;
