import React from "react";
import Styles from "./Notificacao.module.css";

const Notificacao = ({ children, type }) => {
  const [active, setActive] = React.useState(true);

  React.useEffect(() => {
    setActive(true);

    setTimeout(() => {
      setActive(false);
    }, 3000);
  }, []);

  if (active) {
    return (
      <div
        className={`${Styles.not} ${
          type === "alert" ? Styles.alert : Styles.succes
        }`}
      >
        {children}
      </div>
    );
  } else {
    return null;
  }
};

export default Notificacao;
