import React from 'react';
import { GlobalContext } from '../../../../../../GloblStorage';
import Styles from './LoginLojaPage.module.css';
import Loading from '../../../../Partials/Loading';
import LoginLojaInfo from './components/LoginLojaInfo';

const LoginLojaPage = () => {
  const [datas, setDatas] = React.useState(false);

  const Global = React.useContext(GlobalContext);

  const getDatas = async () => {
    const response = await fetch(Global.url + '/prods/login', {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: {}
    });
    const json = await response.json();
    setDatas(json);
  };

  React.useEffect(() => {
    getDatas();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <main className={Styles.container}>
      {datas ? (
        datas.length > 0 ? (
          <>
            <div className={Styles.borderB}>
              <div className={Styles.values}>
                <div>Loja</div>
                <div>Preo</div>
              </div>
            </div>
            {datas.map((l) => {
              return (
                <LoginLojaInfo
                  loja={l.loja}
                  preco={l.preco}
                  estoque={l.estoque}
                  key={l.loja}
                />
              );
            })}
          </>
        ) : (
          <div>No h itens disponiveis</div>
        )
      ) : (
        <Loading />
      )}
    </main>
  );
};

export default LoginLojaPage;
