import React from 'react'
import Styles from "./Dashboard.module.css"
import {GlobalContext} from "../../../../../GloblStorage"

const Dashboard = () => {
    const [msgs, setMsgs] = React.useState(null)
    const [data, setData] = React.useState(null)
    const [money, setMoney] = React.useState(null);

    const Global = React.useContext(GlobalContext);

    function refreshMoney() {
        let userI = localStorage.getItem("userI");
        if(userI) setMoney(Number(JSON.parse(userI).money));
    }

    React.useEffect(() => {
        if(localStorage.getItem('token')) refreshMoney();
    }, [Global, money]);


    const {io} = React.useContext(GlobalContext)

    React.useEffect(() => {
        io.emit("getFeed")
    }, [io])

    io.on("sendMsgs", res => setMsgs(res))

    io.on("sendCountCcs", res => setData(res))

    return (
        <div className={Styles.container}>
            <div className={Styles.title}>Status</div>
            <div className={Styles.cards}>
                <div className={Styles.item}>
                    <div>CCS Disponiveis</div>
                    <div>{data && data.ccCount}</div>
                    <div></div>
                </div>
                <div className={Styles.item}>
                    <div>CCS (pontos)</div>
                    <div>{data && data.ccBonus}</div>

                    <div></div>
                </div>
                <div className={Styles.item}>
                    <div>Saldo</div>
                    <div>R${money !== undefined &&  money !== null ? money.toFixed(2).toString().replace(".",","): "Error"}</div>
                    <div></div>
                </div>
            </div>
            <div className={Styles.title}>Feed</div>
            <div className={Styles.feed}>
                <div className={Styles.scroll}>
                    {msgs ? msgs.map((i, index) => <div key={`${i.data}${index}`}>
                        <div>{i.data}</div>
                        <div>{i.msg}</div>
                    </div>) : <div>Carregando...</div>}
                </div>
            </div>
        </div>
    )
}

export default Dashboard
