import React from "react";
import { GlobalContext } from "../../../../../../GloblStorage";
import Loading from "../../../../Partials/Loading";
import CardIfood from "./Components/CardIfood";
import Styles from "./Page.module.css";

const IfoodPage = () => {
  const [cards, setCards] = React.useState(null);
  const [loading, setLoading] = React.useState(false);

  const Global = React.useContext(GlobalContext);

  React.useState(async () => {
    async function getCards() {
      setLoading(true);
      const response = await fetch(Global.url + "/prods/cards", {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ card: "ifood" }),
      });
      const json = await response.json();
      setLoading(false);
      return json;
    }

    const vals = await getCards();

    setCards(vals);
  }, []);

  return (
    <section className={Styles.container}>
      {cards !== null &&
        cards.map((c) => (
          <CardIfood key={c.value} preco={c.value} estoque={c.estoque} />
        ))}
      {loading && <Loading />}
    </section>
  );
};

export default IfoodPage;
