import React from "react";
import { Link } from "react-router-dom";
import Styles from "./ItemNav.module.css";

const ItemNav = ({ path, children, active, onClick, moreClassName }) => {
  return (
    <>
      <Link
        to={path}
        className={`${Styles.btn} ${active && Styles.active} ${moreClassName}`}
        onClick={onClick}
      >
        {children}
      </Link>
    </>
  );
};

export default ItemNav;
