import React from "react";
import { GlobalContext } from "../../../../../../../GloblStorage";
import Car from "../../../../../../svgs/Car";
import ModalCart from "../../../../../Partials/ModalCart";
import Styles from "./InfoCard.module.css";

const InfoCard = ({
  value,
  vender,
  bin,
  valid,
  pais,
  band,
  tipo,
  nivel,
  emissor,
  cpf,
  nome,
  id,
  type,
  limite,
  itemProd,
}) => {
  const [modal, setModal] = React.useState(false);
  const Global = React.useContext(GlobalContext);

  function handleBuy() {
    setModal(true);
    fetch(Global.url + "/postCart", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify({
        user: Global.userInfos.user,
        prod: {
          prod: "cc",
          newProd: {
            itemProd,
            type: `${type} - ${bin}`,
            id,
            value: Number(value),
            quantidade: 1,
          },
        },
      }),
    });
  }

  return (
    <>
      {modal && <ModalCart setModal={setModal} />}
      <div className={Styles.card}>
        <div className={Styles.btn} onClick={handleBuy}>
          <Car />
        </div>
        <div className={Styles.values}>
          <div>R${value.toFixed(2)}</div>
          <div>{vender}</div>
          <div>{bin}</div>
          <div>{valid}</div>
          <div>{band}</div>
          <div>{pais}</div>
          <div>{tipo}</div>
          <div>{nivel}</div>
          <div>{emissor}</div>
          <div>{cpf ? cpf : "No"}</div>
          <div>{nome ? nome : "No"}</div>
        </div>
      </div>
    </>
  );
};

export default InfoCard;
