import React from 'react';
import Styles from './Button.module.css';

const Button = ({ children, loading, cor, moreStyles, ...props }) => {
  const [pontos, setPontos] = React.useState('');

  setTimeout(() => {
    if (pontos.length < 3) setPontos(pontos + '.');
    else setPontos('.');
  }, 500);

  return (
    <button
      className={`${Styles.button} ${
        cor === 'roxo' ? Styles.roxo : Styles.azul
      }`}
      disabled={loading}
      style={{ ...moreStyles }}
      {...props}
    >
      {loading ? `Aguardando${pontos}` : children}
    </button>
  );
};

export default Button;
