import React from "react";
import { GlobalContext } from "../../../../GloblStorage";
import InfoNav from "./InfoNav";
import LojaNav from "./LojaNav";
import Styles from "./MainArea.module.css";
import NavList from "./NavList";

const MainArea = ({ children }) => {
  const [navActive, setNavActive] = React.useState(window.location.pathname);

  const Global = React.useContext(GlobalContext);

  function handleChangeNav() {
    setTimeout(() => {
      setNavActive(window.location.pathname);
      Global.setPathName(window.location.pathname);
    }, 10);
  }

  return (
    <section className={Styles.container}>
      <div className={Styles.containerNav}>
        <nav>
          {navActive.split("/")[1] === "loja" && (
            <LojaNav
              handleChangeNav={handleChangeNav}
              navActive={navActive}
              setNavActive={setNavActive}
            />
          )}
          <NavList
            handleChangeNav={handleChangeNav}
            active={
              navActive.split("/")[1] !== "loja" &&
              navActive.split("/")[1] !== "minhas-infos"
            }
            navActive={navActive}
            pages={[
              { name: "Loja", url: "/loja" },
              { name: "Regras", url: "/regras" },
              { name: "Minhas Infos", url: "/minhas-infos" },
              { name: "Carrinho", url: "/carrinho" },
            ]}
          />
          {/* <NavList
            handleChangeNav={handleChangeNav}
            active={navActive.split("/")[1] === "minhas-infos"}
            navActive={navActive}
            pages={[
              { name: "CC", url: "minhas-infos/cc" },
              { name: "Gift", url: "minhas-infos/Gift" },
              { name: "Consultavel", url: "minhas-infos/consultavel" },
              { name: "Lara", url: "minhas-infos/lara" },
            ]}
          /> */}
          <InfoNav
            setNavActive={setNavActive}
            handleChangeNav={handleChangeNav}
            navActive={navActive}
          />
        </nav>
      </div>
      <main>{children}</main>
    </section>
  );
};

export default MainArea;
