import React from 'react';
import Styles from './LoginPageInfo.module.css';
import { GlobalContext } from '../../../../../../../GloblStorage';
import Loading from '../../../../../Partials/Loading';
import LoginInfo from './components/LoginInfo';

const LoginPageInfo = () => {
  const [data, setData] = React.useState(false);

  const Global = React.useContext(GlobalContext);

  React.useEffect(() => {
    async function getDatas() {
      let token = localStorage.getItem('token');
      const response = await fetch(Global.url + '/getInfos', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ type: 'login', token: token })
      });
      const json = await response.json();
      setData(json);
    }

    getDatas();
  }, [Global]);

  return (
    <main className={Styles.container}>
      <div className={Styles.content}>
        {data ? (
          data.length > 0 ? (
            <>
              <div className={Styles.titles}>
                <div></div>
                <div>Loja</div>
                <div>Email</div>
                <div>Senha</div>
                <div>Nome</div>
                <div>CPF</div>
                <div>Estado</div>
              </div>
              {data.reverse().map((l) => (
                <LoginInfo {...l} />
              ))}
            </>
          ) : (
            <div className={Styles.err}>No h itns disponiveis</div>
          )
        ) : (
          <Loading />
        )}
      </div>
    </main>
  );
};

export default LoginPageInfo;
