import React from "react";
import { GlobalContext } from "../../../../../../../GloblStorage";
import Car from "../../../../../../svgs/Car";
import ModalCart from "../../../../../Partials/ModalCart";
import Styles from "./InfoLara.module.css";

const InfoLara = ({
  value,
  vendedor,
  titular,
  banco,
  pix,
  cpf,
  nome,
  nivel,
  id,
}) => {
  const [modal, setModal] = React.useState(false);
  const Global = React.useContext(GlobalContext);

  function handleBuy() {
    setModal(true);
    fetch(Global.url + "/postCart", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify({
        user: Global.userInfos.user,
        prod: {
          prod: "lara",
          newProd: {
            type: `${"Lara"} - Nivel: ${nivel}`,
            id,
            value: Number(value),
            quantidade: 1,
          },
        },
      }),
    });
  }

  return (
    <>
      {modal && <ModalCart setModal={setModal} />}
      <div className={Styles.card}>
        <div className={Styles.btn} onClick={handleBuy}>
          <Car />
        </div>
        <div className={Styles.values}>
          <div>{`R$${value.toFixed(2).toString().replace(".", ",")}`}</div>
          <div>{vendedor}</div>
          <div>{titular}</div>
          <div>{banco}</div>
          <div>{pix ? "Sim" : "No"}</div>
          <div>{cpf ? "Sim" : "No"}</div>
          <div>{nome ? "Sim" : "No"}</div>
          <div>{nivel}</div>
        </div>
      </div>
    </>
  );
};

export default InfoLara;
