import React from "react";
import { GlobalContext } from "../../../../../../../GloblStorage";
import Button from "../../../../../../forms/Button";
import Car from "../../../../../../svgs/Car";
import Loading from "../../../../../Partials/Loading";
import Styles from "./InfoCard.module.css";
import StylesModal from  '../../../../../Partials/ModalCart.module.css'

const CcBonusCart = ({
  value,
  vender,
  bin,
  valid,
  pais,
  band,
  tipo,
  nivel,
  emissor,
  cpf,
  nome,
  id,
  type,
  limite,
  itemProd,
}) => {
  const [modal, setModal] = React.useState(false);
  const [datasBuy, setDatasBuy] = React.useState(false);
  const Global = React.useContext(GlobalContext);

  function handleBuy() {
    let token = localStorage.getItem("token")
    setModal(true)
    fetch(Global.url + "/buyCcBonus", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      }, 
      body: JSON.stringify({
        token,
        idCc: id
      }),
    })
    .then(res => res.json())
    .then(json => setDatasBuy(json));
  }

  function goToInfos() {
    window.location.pathname = "/minhas-infos/cc"
  }

  function closeModal(){
    setModal(false)
  }

  return (
    <>
      {modal && 
      <div className={StylesModal.modal} id="modal" onClick={closeModal}>
        <div className={StylesModal.card}>
          {datasBuy ? datasBuy.error ?
            <div>{datasBuy.message}</div> 
            :
          <>
            <div>Item comprado com sucesso!</div>
            <Button cor="roxo" onClick={goToInfos}>
                Ir para minhas Infos
            </Button>
          </>
          :
            <Loading />
          }
      </div>
      </div>
      }
      <div className={Styles.card}>
        <div className={Styles.btn} onClick={handleBuy}>
          <Car />
        </div>
        <div className={Styles.values}>
          <div>{value} Pts.</div>
          <div>{vender}</div>
          <div>{bin}</div>
          <div>{valid}</div>
          <div>{band}</div>
          <div>{pais}</div>
          <div>{tipo}</div>
          <div>{nivel}</div>
          <div>{emissor}</div>
          <div>{cpf ? cpf : "No"}</div>
          <div>{nome ? nome : "No"}</div>
        </div>
      </div>
    </>
  );
};

export default CcBonusCart;
