import React from "react";
import { GlobalContext } from "../../../../../../GloblStorage";
import Loading from "../../../../Partials/Loading";
import CardNetflix from "./Components/CardNetflix";
import Styles from "./Page.module.css";

const NetflixPage = () => {
  const [cards, setCards] = React.useState([]);
  const [loading, setLoading] = React.useState(false);

  const Global = React.useContext(GlobalContext);

  React.useState(async () => {
    async function getCards() {
      setLoading(true);
      const response = await fetch(Global.url + "/prods/cards", {
        method: "post",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ card: "netflix" }),
      });
      const json = await response.json();
      setCards(json);
      setLoading(false);
      return json;
    }
    getCards();
  }, []);

  return (
    <section className={Styles.container}>
      {loading && <Loading />}
      {cards &&
        cards.map(({ value, plano, estoque }) => (
          <CardNetflix
            key={plano}
            preco={value.toFixed(2)}
            title={plano}
            estoque={estoque}
          />
        ))}
    </section>
  );
};

export default NetflixPage;
