import React from 'react'
import { GlobalContext } from '../../../../../../GloblStorage'
import Loading from '../../../../Partials/Loading'
import Styles from "./CcBonus.module.css"
import CcBonusCart from './Components/CcBonusCart'


const CcBonus = () => {
  const [data, setData] = React.useState(false)
  const [pontos, setPontos] = React.useState(null);
  const [pages, setPages] = React.useState(null);
  const [pageActive, setPageActive] = React.useState(0);

  const Global = React.useContext(GlobalContext)

  const getDatas = async () => {
    if(Global.userInfos.user){
      const responseUser = await fetch(Global.url + "/getCcBonusUser/" + Global.userInfos.user)
      const jsonUser = await responseUser.json();
      setPontos(jsonUser.value)
     
      const responseCc = await fetch(Global.url + "/getCcBonus");
      const jsonCc = await responseCc.json();
      if(jsonCc.length < 1) return setPages([[]])
      let arr = [];
      let countPagesNum = (Math.ceil(jsonCc.length / 30))
      for (let i = 0; i < countPagesNum; i++){
        arr.push(jsonCc.slice(i * 30, (i + 1) * 30))
      }
      setPages(arr)
      setData(jsonCc);
    }
  }

  React.useEffect(() => {
    if(Global.userInfos) getDatas();
  // eslint-disable-next-line 
  }, [Global.userInfos])

  return (
    <section className={Styles.container} id={Styles.titles}>
      <div className={Styles.saldo}>
        <span>Saldo: </span>
        <span>{pontos && pontos} pts</span>
      </div>
      <div className={Styles.aviso}>Ao Clicar para comprar voc ja finaliza a compra</div>
      <div className={Styles.containerCcs}>
      {pages ? (
        pages[0].length > 0 ? (
          <>
            <div className={Styles.titles}>
              <div>Valor</div>
              <div>Vendedor</div>
              <div>BIN</div>
              <div>Validade</div>
              <div>Bandeira</div>
              <div>Pais</div>
              <div>Tipo</div>
              <div>Nivel</div>
              <div>Emissor</div>
              <div>CPF</div>
              <div>Nome</div>
            </div>
            {pages[pageActive].map((card) => (
              <CcBonusCart {...card} type="Cc Card" itemProd="cc" />
            ))}
            <div className={Styles.nav}>              
              {pages.length > 1 &&pages.map((list, index) =>
                <a href={"#" + Styles.titles}>
                  <div className={index === pageActive && Styles.active} onClick={() => setPageActive(index)}>
                    {index + 1}
                  </div>
                </a>)}
            </div>
          </>
        ) : (
          <div className={Styles.err}>No h itens disponiveis</div>
        )
      ) : (
        <Loading />
      )}
      </div>
    </section>
  )
}

export default CcBonus
