import React from "react";
import Notificacao from "./Notificacao";
import Styles from "./NotificacaoContainer.module.css";
import { GlobalContext } from "../../../GloblStorage";

const NotificacaoContainer = () => {
  const global = React.useContext(GlobalContext);
  const [nots, setNots] = React.useState([]);

  React.useEffect(() => {
    setNots(global.nots);
  }, [global, nots]);

  return (
    <div className={Styles.container}>
      {nots.length > 0
        ? nots.map((not) => {
            return (
              <Notificacao key={not.id} type={not.type}>
                {not.msg}
              </Notificacao>
            );
          })
        : null}
    </div>
  );
};

export default NotificacaoContainer;
