import React from "react";
import Styles from "./Main.module.css";
// import { Redirect } from "react-dom"
import { Navigate } from 'react-router-dom'

const Main = ({ children }) => {
  const [logad, setLogad] = React.useState(localStorage.getItem("token") ? true : false)

  // if (!localStorage.getItem("token")) setLogad(false);

  React.useEffect(() => {
    setLogad(localStorage.getItem('token') ? true : false)
    if (!localStorage.getItem('remember') && !window.location.pathname.includes("minha-conta")) {
      localStorage.setItem('loginCont', +(+localStorage.getItem('loginCont') + 1))
      if (localStorage.getItem('loginCont') > 1) {
        localStorage.removeItem('token')
        localStorage.removeItem('userI')
        localStorage.removeItem('remember')
        localStorage.removeItem('loginCont')
        window.location.pathname = "/login";
      }
    }
  }, [])

  return <main className={Styles.main}>
    {!logad && <Navigate to='/login'/>}
    {children}
  </main>;
};

export default Main;
