import React from "react";
import NavList from "./NavList";
import Styles from "./LojaNav.module.css";
import Home from "../../../svgs/Home";

const LojaNav = ({ handleChangeNav, navActive, setNavActive }) => {
  const [navPageActive, setNavPageActive] = React.useState("principal");
  const [princNav, setPrinceNav] = React.useState(true);

  return (
    <>
      <div className={`${Styles.btn} ${!princNav && Styles.active}`}>
        <div
          onClick={() => {
            if (navPageActive !== "principal") setPrinceNav(!princNav);
          }}
        >
          +
        </div>
        <div
          onClick={() => {
            setNavActive("/");
          }}
        >
          <Home size="20" />
        </div>
      </div>

      <NavList
        active={princNav}
        pages={[
          { name: "CC", url: "/loja/cc", toSubNav: "CC" },
          { name: "Gift Card", url: "/loja/gift-card", toSubNav: "Gift" },
          {
            name: "Consutavel",
            url: "/loja/consultavel",
            toSubNav: "Consutaveis",
          },
          { name: "Lara", url: "/loja/lara" },
          { name: "Login", url: "/loja/login" },
        ]}
        subNav={true}
        navActive={navActive}
        handleChangeNav={handleChangeNav}
        setNavPage={setNavPageActive}
        setPrinceNav={setPrinceNav}
      />

      {!princNav && (
        <>
          {/* Gift */}
          <NavList
            active={navPageActive === "Gift"}
            pages={[
              {
                name: "Ifood",
                url: "/loja/gift-card/Ifood",
              },
              {
                name: "Uber eats",
                url: "/loja/gift-card/uber-eats",
              },
              {
                name: "Uber",
                url: "/loja/gift-card/Uber",
              },
              {
                name: "Netflix",
                url: "/loja/gift-card/Netflix",
              },
              {
                name: "GooglePlay",
                url: "/loja/gift-card/GooglePlay",
              },
            ]}
            subNav={true}
            navActive={navActive}
            handleChangeNav={handleChangeNav}
            setNavPage={setNavPageActive}
            setPrinceNav={setPrinceNav}
          />
          {/* Consutaveis */}
          <NavList
            active={navPageActive === "Consutaveis"}
            pages={[
              {
                name: "Itau",
                url: "/loja/consultavel/Itau",
              },
              {
                name: "Magazine",
                url: "/loja/consultavel/Magazine",
              },
              {
                name: "Hipercard",
                url: "/loja/consultavel/Hipercard",
              },
              {
                name: "Credicard",
                url: "/loja/consultavel/Credicard",
              },
              {
                name: "Santander",
                url: "/loja/consultavel/Santander",
              },
            ]}
            subNav={true}
            navActive={navActive}
            handleChangeNav={handleChangeNav}
            setNavPage={setNavPageActive}
            setPrinceNav={setPrinceNav}
          />
          {/* cc */}
          <NavList
            active={navPageActive === "CC"}
            pages={[
              {
                name: "CC = Card",
                url: "/loja/cc/card",
              },
              {
                name: "CC + Nome",
                url: "/loja/cc/nome",
              },
              {
                name: "CC Full",
                url: "/loja/cc/full",
              },
              {
                name: "CC Consultada",
                url: "/loja/cc/consultada",
              },
              {
                name: "CC bonus",
                url: "/loja/cc/bonus",
              },
            ]}
            subNav={true}
            navActive={navActive}
            handleChangeNav={handleChangeNav}
            setNavPage={setNavPageActive}
            setPrinceNav={setPrinceNav}
          />
        </>
      )}
    </>
  );
};

export default LojaNav;
