import React from "react";
import { Routes, Route } from "react-router";
import CcBonus from "./CcBonus";
import CcCard from "./CcCard";
import CcCardConsulta from "./CcCardConsuta";
import CcCardFull from "./CcCardFull";
import CcCardName from "./CcCardName";

const CcRouter = () => {
  return (
    <Routes>
      <Route path="card" element={<CcCard />} />
      <Route path="nome" element={<CcCardName />} />
      <Route path="full" element={<CcCardFull />} />
      <Route path="consultada" element={<CcCardConsulta />} />
      <Route path="bonus" element={<CcBonus />} />
    </Routes>
  );
};

export default CcRouter;
