import React from 'react';
import Styles from './Consultavel.module.css';
import Loading from '../../../../Partials/Loading';
import { GlobalContext } from '../../../../../../GloblStorage';
import InfoConsul from './Components/InfoConsul';

const Consultavel = () => {


  const [data, setData] = React.useState(null);
  const [isPage, setIsPage] = React.useState(false);
  const [loja, setLoja] = React.useState(
    window.location.pathname.split('/').at(-1)
  );
  const [loading, setLoading] = React.useState(false);

  const Global = React.useContext(GlobalContext);

  React.useEffect(() => {
    setLoja(Global.pathnameState.split('/').at(-1));
  }, [Global.pathnameState, Global.url]);

  React.useEffect(() => {
    setLoading(true);
    setData([]);
    if(Global.pathnameState.split('/').length > 3 ) {
      setIsPage(true)
        fetch(Global.url + '/consultavel/' + loja.toLowerCase(), {
        method: 'POST',
        headers: {
          "Content-Type":"application/json"
        }
      }
      )
        .then(async (res) => {
          return res.json();
        })
        .catch(setLoading(false))
        .then((json) => {
          console.log(json);
          setData(json);
          setLoading(false);
        })
        .catch(setLoading(false));
    }else{
      setIsPage(false)
      setLoading(false)
    }
  
  
    }, [loja, Global.url]);

    console.log(isPage);


  return (
    <div className={Styles.container}>
      {loading && <Loading />}
      {data && data.length > 0 ? (
        <>
          <div className={Styles.containerCards}>
            {data.map((l) => (
              <InfoConsul {...l} key={l.id} loja={loja.toLowerCase()} />
            ))}
          </div>
        </>
      ) : (
        <div className={Styles.err}>{isPage && <>No ha itens disponivel</>}</div>
      )}
    </div>
  );
};
export default Consultavel;
