import React from 'react';
import Styles from './GiftInfoPage.module.css';
import Loading from '../../../../../Partials/Loading';
import { GlobalContext } from '../../../../../../../GloblStorage';
import GiftInfo from './GiftInfo';

const CcCard = () => {
  const [data, setData] = React.useState(null);

  const Global = React.useContext(GlobalContext);

  React.useEffect(() => {
    async function getDatas() {
      let token = localStorage.getItem('token');
      const response = await fetch(Global.url + '/getInfos', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ type: 'gift', token: token })
      });
      const json = await response.json();
      setData(json);
    }

    getDatas();
  }, [Global]);

  return (
    <div className={Styles.container}>
      {data ? (
        <>
          <div className={Styles.containerTitle}>
            <div className={Styles.titles}>
              <div></div>
              <div>Tipo</div>
              <div>Valor</div>
              <div>Codigo</div>
            </div>
          </div>
          {data.reverse().map((card) => (
            <GiftInfo
              type={card.type}
              value={card.value}
              code={card.key}
              key={card.key}
              data={card.data}
            />
          ))}
        </>
      ) : (
        <Loading />
      )}
    </div>
  );
};

export default CcCard;
