import React from "react";
import Home from "../../../svgs/Home";
import NavList from "./NavList";
import Styles from "./InfoNav.module.css";

const InfoNav = ({ setNavActive, handleChangeNav, navActive }) => {
  if (navActive.split("/")[1] === "minhas-infos")
    return (
      <>
        <div className={`${Styles.btn}`}>
          <div
            onClick={() => {
              setNavActive("/");
            }}
          >
            <Home size="20" />
          </div>
        </div>

        <NavList
          handleChangeNav={handleChangeNav}
          active={navActive.split("/")[1] === "minhas-infos"}
          navActive={navActive}
          pages={[
            { name: "CC", url: "minhas-infos/cc" },
            { name: "CC BONUS", url: "minhas-infos/bonuscc" },
            { name: "Gift", url: "minhas-infos/Gift" },
            { name: "Consultavel", url: "minhas-infos/consultavel" },
            { name: "Lara", url: "minhas-infos/lara" },
            { name: "Login", url: "minhas-infos/login" },
          ]}
        />
      </>
    );
  else return null;
};

export default InfoNav;
