import React from 'react';
import Styles from './CcInfoPage.module.css';
import Loading from '../../../../../Partials/Loading';
import MinhaInfoCardConsul from './components/MinhaInfoCardConsul';
import { GlobalContext } from '../../../../../../../GloblStorage';

const CcInfoPage = () => {
  const [data, setData] = React.useState(null);
  const [loading, setLoading] = React.useState(null);

  const Global = React.useContext(GlobalContext);

  React.useEffect(() => {
    async function getDatas() {
      try {
        let token = localStorage.getItem('token');
        setLoading(true);
        const response = await fetch(Global.url + '/getInfos', {
          method: 'POST',
          headers: {
            'Content-Type': 'application/json'
          },
          body: JSON.stringify({ type: 'cc', token })
        });
        const json = await response.json();
        setData(json.reverse());
      } catch {
      } finally {
        setLoading(false);
      }
    }

    getDatas();
  }, [Global]);

  return (
    <div className={Styles.container}>
      {data && (
        <>
          <div className={Styles.titles}>
            <div></div>
            <div>Numero</div>
            <div>Validade</div>
            <div>CVV</div>
            <div>CPF</div>
            <div>Nome</div>
            <div>Limite</div>
          </div>
          {data.reverse().map((card) => (
            <MinhaInfoCardConsul {...card} key={card.id} type="Cc Card" />
          ))}
        </>
      )}
      {loading && <Loading />}
    </div>
  );
};

export default CcInfoPage;
