import React from "react";
import Styles from "./CardGooglePlay.module.css";
import imgCard from "../../../../../../../assets/gifts/googleplay.png";
import { GlobalContext } from "../../../../../../../GloblStorage";
import ModalCart from "../../../../../Partials/ModalCart";

const CardGooglePlay = ({ preco, estoque }) => {
  const [modal, setModal] = React.useState(false);
  const Global = React.useContext(GlobalContext);

  async function handleBuy() {
    const res = await fetch(Global.url + "/postCart", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify({
        user: Global.userInfos.user,
        prod: {
          prod: "gift",
          newProd: {
            type: "googleplay",
            value: Number(preco),
            quantidade: 1,
          },
        },
      }),
    });
    const json = await res.json();
    if (json.error) {
      Global.newNotification(json.message, "alert");
    } else {
      setModal(true);
    }
  }

  return (
    <>
      {modal && <ModalCart setModal={setModal} />}
      <div className={`${Styles.card} ${estoque ? null : Styles.noStock}`}>
        <div className={Styles.promo}>Gift Card Qualquer Marca Paga 50%-</div>
        <img src={imgCard} alt="" />
        <div className={Styles.infos}>
          <h2>Google Play</h2>
          <h2>R$ {preco} </h2>
        </div>
        <button onClick={estoque && handleBuy}>Comprar</button>
      </div>
    </>
  );
};

export default CardGooglePlay;
