import React from "react";
import Regra from "./components/Regra";
import Styles from "./Regras.module.css";

const Regras = () => {
  return (
    <section className={Styles.container}>
      <Regra
        title="Oque no  permitido como usurio?"
        text={
          <>
            No  permitido nada que venha prejudicar a RYCA Store como: <br />
            - Chingamentos
            <br />
            - Reclamaes sem motivos
            <br />
            - Ataques
            <br />- Tentativa de burlar o sistema
          </>
        }
      />
      <Regra
        title="Como efetuar uma compra?"
        text={
          <>
            Aps carregar sua conta com os mtodos de pagamentos disponveis no
            site, voc dever entrar em loja, escolher um produto e clicar no
            boto ao lado em comprar.
          </>
        }
      />
      <Regra
        title=" permitido estornar um carto?"
        text={
          <>
            Aps uma atualizao ns decidimos reduzir os preos das infos e
            desabilitar o sistema de estornos, assim o usurio paga um preo
            mais acessvel pela info.
          </>
        }
      />
      <Regra
        title=" possivel fazer o estorno de uma recarga em minha conta? "
        text={<>Ns no fazemos o estorno.</>}
      />
      <Regra
        title="Quais so os preos dos carto? "
        text={
          <>
            Os preos so variados de acordo com o nvel ou bandeira do carto,
            ele pode variar de 4,50 at 45,00 reais.
          </>
        }
      />
      <Regra
        title="Comprei um carto e est sem saldo, oque fazer?"
        text={
          <>
            Nesse caso, no nos responsabilizamos por carto que esteja sem
            saldo, nossa garantia  que o seu carto esteja bom, e no com saldo
            disponvel para realizao de sua aprovao. <br />
            Podemos aconselhar guardar o carto at que a fatura de carto de
            crdito vire e voc tenha mais saldo no mesmo.
          </>
        }
      />
      <Regra
        title=" possivel ver o saldo do carto?"
        text={
          <>
            No, o saldo  indeterminado, ento pode acontecer de cartes virem
            com pouco saldo ou muito saldo. Sendo a info cc consultada 
            possivel saber o saldo.
          </>
        }
      />
      <Regra
        title="Como escolher um carto especifico?"
        text={
          <>
            Para escolher um carto especfico voc tem as opes para
            classificar os cartes por: bin, level, tipo, bandeira, maior preo,
            menor preo etc.
          </>
        }
      />
    </section>
  );
};

export default Regras;
