import React from "react";
import Styles from "./ProdCart.module.css";
import { GlobalContext } from "../../../../GloblStorage";

const ProdCart = ({
  itemType,
  type,
  preco,
  quantidade,
  nome,
  setUpdate,
  setLoading,
  estoque,
  loja,
}) => {
  const Global = React.useContext(GlobalContext);

  async function removeItem() {
    setLoading(true);
    setTimeout(() => {
      setUpdate();
    }, 1000);
    fetch(Global.url + "/removeCart", {
      method: "POST",
      headers: {
        "content-type": "application/json",
      },
      body: JSON.stringify({
        user: Global.userInfos.user,
        prod: {
          type,
          value: preco,
          prod: itemType,
        },
      }),
    });
  }

  async function addMoreOne() {
    if (itemType === "gift") {
      if (quantidade >= estoque)
        return Global.newNotification("Estoque Insuficiente", "alert");
    }
    if (itemType === "login") {
      if (quantidade >= estoque)
        return Global.newNotification("Estoque Insuficiente", "alert");
    }
    setLoading(true);
    setTimeout(() => {
      setUpdate();
    }, 1000);

    if (itemType === "login") {
      fetch(Global.url + "/addMoreOne", {
        method: "POST",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify({
          user: Global.userInfos.user,
          prod: {
            prod: itemType,
            prodToAdd: {
              type,
              value: preco,
              loja,
            },
          },
        }),
      });
    } else {
      fetch(Global.url + "/addMoreOne", {
        method: "POST",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify({
          user: Global.userInfos.user,
          prod: {
            prod: itemType,
            prodToAdd: {
              type,
              value: preco,
            },
          },
        }),
      });
    }

    // await fetch(Global.url + "/addMoreOne", {
    //   method: "POST",
    //   headers: {
    //     "content-type": "application/json",
    //   },
    //   body: JSON.stringify({
    //     user: Global.userInfos.user,
    //     prod: {
    //       prod: itemType,
    //       prodToAdd: {
    //         type,
    //         value: preco,
    //       },
    //     },
    //   }),
    // });
  }

  async function rmMoreOne() {
    setLoading(true);
    setTimeout(() => {
      setUpdate();
    }, 1000);
    if(quantidade <= 1) return removeItem();

    if (itemType === "login") {
      fetch(Global.url + "/rmMoreOne", {
        method: "POST",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify({
          user: Global.userInfos.user,
          prod: {
            prod: itemType,
            prodToAdd: {
              type,
              value: preco,
              loja,
            },
          },
        }),
      });
    } else {
      fetch(Global.url + "/rmMoreOne", {
        method: "POST",
        headers: {
          "content-type": "application/json",
        },
        body: JSON.stringify({
          user: Global.userInfos.user,
          prod: {
            prod: itemType,
            prodToAdd: {
              type,
              value: preco,
            },
          },
        }),
      });
    }
  }

  return (
    <div className={Styles.produto}>
      <div className={Styles.name}>
        {/* GIFTS */}
        {type === "ifood" && "Ifood"}
        {type === "googleplay" && "Google Play"}
        {type === "netflix" && "Netflix"}
        {type === "uber" && "Uber"}
        {type === "ubereats" && "Uber Eats"}
        {/* CCS */}
        {itemType === "cc" && type}
        {itemType === "lara" && type}
        {itemType === "login" && type}
        {itemType === "consultaveis" && type}
      </div>
      <div className={Styles.quant}>
        {itemType === "gift" || itemType === "login" ? (
          <>
            <span onClick={rmMoreOne}>-</span>
            <div>{quantidade}</div>
            <span onClick={addMoreOne}>+</span>
          </>
        ) : (
          <div className={Styles.quantText}>{quantidade}</div>
        )}
      </div>
      <div>R${preco}</div>
      <div onClick={removeItem} className={Styles.btnClose}>
        +
      </div>
    </div>
  );
};

export default ProdCart;
