import React from "react";
import Styles from "./NavList.module.css";
import ItemNav from "./ItemNav";

const NavList = ({
  active,
  pages,
  navActive,
  handleChangeNav,
  subNav,
  setNavPage,
  setPrinceNav,
}) => {
  return active ? (
    <div className={Styles.nav}>
      {subNav && <div className={Styles.btn}></div>}
      {pages.map(({ url, name, toSubNav }) => (
        <ItemNav
          path={url}
          active={navActive.includes(url)}
          onClick={() => {
            handleChangeNav();
            if (toSubNav) {
              setPrinceNav(false);
              setNavPage(toSubNav);
            }
          }}
          key={name}
        >
          {name}
        </ItemNav>
      ))}
    </div>
  ) : null;
};

export default NavList;
