import React from 'react';
import { GlobalContext } from '../../../../../../../../GloblStorage';
import Button from '../../../../../../../forms/Button';
import Copy from '../../../../../../../svgs/Copy';
import Trash from '../../../../../../../svgs/Trash';
import ModalCart from '../../../../../../Partials/ModalCart';
import Styles from './MinhaInfoCardConsul.module.css';

const MinhaInfoCardBonus = ({
  value,
  vender,
  bin,
  valid,
  numero,
  pais,
  band,
  tipo,
  nivel,
  emissor,
  cpf,
  nome,
  limit,
  data,
  type,
  id,
  cvv
}) => {
  const [modal, setModal] = React.useState(false);
  const [modalDelete, setModalDelete] = React.useState(false);
  const d = new Date();
  const today = `${d.getDay()}${d.getMonth()}${d.getFullYear()}`;
  const [copyed, setCopyed] = React.useState(false);
  const [deleted, setDeleted] = React.useState(false);

  const Global = React.useContext(GlobalContext);

  const CopyFunc = () => {
    var textArea = document.createElement('textarea');
    textArea.value = `${numero}|${valid.split('/')[0].padStart(2, '0')}|${valid
      .split('/')[1]
      .padStart(4, '20')}|${cvv}`;

    textArea.style.top = '150vh';
    textArea.style.left = '0';
    textArea.style.position = 'fixed';

    document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();

    document.execCommand('copy');

    setCopyed(true);
    setTimeout(() => {
      setCopyed(false);
    }, 1000);

    document.body.removeChild(textArea);
  };

  const closeModal = () => {
    setModalDelete(false);
  };

  const openModal = () => {
    setModalDelete(true);
  };

  const removeInfo = () => {
    const token = localStorage.getItem('token');
    fetch(Global.url + '/removeInfo/ccbonus', {
      method: 'POST',
      headers: {
        'content-type': 'application/json'
      },
      body: JSON.stringify({
        token,
        id
      })
    }).then(() => {
      setModalDelete(false);
      setDeleted(true);
    });
  };

  if (deleted) return null;

  return (
    <>
      {modalDelete && (
        <div
          className={Styles.modalDelContainer}
          id="modalBackgrond"
          onClick={(e) => {
            if (e.target.id === 'modalBackgrond') closeModal();
          }}
        >
          <div className={Styles.modalDel}>
            <div className={Styles.title}>Tem Certeza disso?</div>
            <div className={Styles.btns}>
              <Button onClick={removeInfo}>Confirmar</Button>
              <Button onClick={closeModal}>Cancelar</Button>
            </div>
          </div>
        </div>
      )}
      {modal && <ModalCart setModal={setModal} />}
      <div className={Styles.card}>
        <div className={Styles.values}>
          <div className={Styles.new}>{data === today && <div>new</div>}</div>
          <div>{numero}</div>
          <div>{valid}</div>
          <div>{cvv}</div>
          <div>{cpf ? cpf : 'No'}</div>
          <div>{nome ? nome : 'No'}</div>
          <div>{limit ? limit : 'No'}</div>
          <div
            id={Styles.copyBtn}
            className={Styles.copyBtn}
            onClick={CopyFunc}
          >
            <Copy color={copyed ? '#0f0' : '#ba54f5'} />
          </div>
          <div onClick={openModal}>
            <Trash color="#ba0019" />
          </div>
        </div>
      </div>
    </>
  );
};

export default MinhaInfoCardBonus;
