import React from "react";
import { GlobalContext } from "../../../../../../../GloblStorage";
import Styles from "./ExtratoPage.module.css";
import Loading from "../../../../../Partials/Loading"
import ItemHistorico from "./components/ItemHistorico";

const ExtratoPage = () => {
  const [historico, setHistorico] = React.useState([])
  const [loading, setLoading] = React.useState(true);

  const { io } = React.useContext(GlobalContext);
  
  io.on("historico", (his) => {
    setLoading(false);
    // lethistore = 
    setHistorico(his);
  })
  
  React.useEffect(() => {
    let token = localStorage.getItem('token')
    io.emit("getHistory", { token })
  }, [])

  return (
    <div className={Styles.container}>
      <div className={Styles.title}>Extrato</div>
      <div className={Styles.historico}>
        <div className={Styles.subTitle}>Historico</div>
        {loading ?
          <div className={Styles.loading}></div>
          : historico.length > 0 ?
            <div className={Styles.itens}>
              <div className={Styles.titles}>
                <div>Data</div>
                <div>Valor</div>
                <div>Status</div>
              </div>
              {historico.map(h => <ItemHistorico {...h} />)}
            </div> :
            <div className={Styles.aviso}></div>}
      </div>
    </div>
  );
};

export default ExtratoPage;
