import React from "react";
import Styles from "./Header.module.css";
import { GlobalContext } from "../../../GloblStorage";
import User from "../../svgs/User";
import Money from "./Money";
import AddMoney from "./AddMoney";
import img from "../../../assets/images/title.png";
import { Link } from "react-router-dom";

const Header = () => {
  const [navActive, setNavActive] = React.useState(false);
  const [lojaActive, setLojaActive] = React.useState(false);

  const global = React.useContext(GlobalContext);

  function handleClick() {
    setNavActive(!navActive);
  }

  function handleLogOut() {
    localStorage.removeItem("token");
    localStorage.removeItem("userI");
    window.location.pathname = "/login";
  }

  return (
    <header className={Styles.header}>
      <div className={Styles.txt}><Link to="/loja">Painel do Comprador</Link></div>
      <div className={Styles.logo}>
        <Link to="/">
          <img src={img} alt="" />
        </Link>
        {/* <h2>
          <span>RYCA</span> STORE
        </h2>
        <p>A Loja N 1 do Brasil </p> */}
      </div>
      <div className={Styles.btns}>
        <div
          className={`${Styles.user} ${navActive && Styles.active}`}
          onClick={handleClick}
        >
          <User size="24" />
          {global.userInfos && global.userInfos.user}
        </div>
        <nav className={`${Styles.nav} ${navActive && Styles.active}`}>
          <ul>
            <li><Link to="/minha-conta">Minha Conta</Link></li>
            <li onClick={handleLogOut}>Sair</li>
          </ul>
        </nav>
      </div>
      <Money setActive={setLojaActive} />
      <AddMoney active={lojaActive} setActive={setLojaActive} />
    </header>
  );
};

export default Header;
