import React from "react";
import { Route, Routes } from "react-router-dom";
import CcRouter from "./Pages/CC/CcRouter";
import Consultavel from "./Pages/Consultavel/Consultavel";
import GiftRouter from "./Pages/Gifts/GiftRouter";
import LaraPage from "./Pages/Lara/LaraPage";
import LoginLojaPage from "./Pages/Login/LoginLojaPage";

const LojaRouter = () => {
  return (
    <Routes>
      <Route path="gift-card/*" element={<GiftRouter />} />
      <Route path="cc/*" element={<CcRouter />} />
      <Route path="lara/*" element={<LaraPage />} />
      <Route path="consultavel/*" element={<Consultavel />} />
      <Route path="login/*" element={<LoginLojaPage />} />
    </Routes>
  );
};

export default LojaRouter;
