import React from 'react';
import Styles from './CartPage.module.css';
import Button from '../../forms/Button';
import Loading from '../Partials/Loading';
import ProdCart from './components/ProdCart';
import { GlobalContext } from '../../../GloblStorage';
import io from 'socket.io-client';
import { Link } from 'react-router-dom';

const ioClient = io('');

const CartPage = () => {
  const Global = React.useContext(GlobalContext);
  const [prodsInfos, setProdsInfos] = React.useState(false);
  const [loading, setLoading] = React.useState(false);
  const [loadingBuy, setLoadingBuy] = React.useState(false);
  const [statusPay, setStatusPay] = React.useState(false);
  const [modal, setModal] = React.useState(false);
  const [checkar, setCheckar] = React.useState(false);

  async function refreshDatas() {
    setLoading(true);
    const response = await fetch(Global.url + '/getCart', {
      method: 'POST',
      headers: {
        'Content-type': 'application/json'
      },
      body: JSON.stringify({
        user: Global.userInfos.user
      })
    });
    const json = await response.json();
    setProdsInfos(json);
    setLoading(false);
  }

  React.useEffect(() => {
    if (Global.userInfos.user)
      fetch(Global.url + '/getCart', {
        method: 'POST',
        headers: {
          'Content-type': 'application/json'
        },
        body: JSON.stringify({
          user: Global.userInfos.user
        })
      })
        .then((res) => res.json())
        .then((json) => setProdsInfos(json));
  }, [Global]);

  async function buy() {
    console.log('oii');

    let money = JSON.parse(localStorage.getItem('userI')).money;
    if (!prodsInfos)
      return Global.newNotification('Nenhum item no carrinho', 'alert');
    else if (money < prodsInfos.totalPreco)
      return Global.newNotification('Voc no tem Saldo Suficiente', 'alert');
    else {
      setLoadingBuy(true);
      setModal(true);
      const d = new Date();
      const dataToday = `${d.getDay()}${d.getMonth()}${d.getFullYear()}`;
      let token = localStorage.getItem('token');
      console.log( token, checkar, dataToday);
      ioClient.emit('buyItensCards', { token, checkar, dataToday });
    }
  }

  const [ccsRes, setCcsRes] = React.useState([]);

  ioClient.on('ccChecked', (cc) => {
    setCcsRes([...ccsRes, cc]);
  });

  ioClient.on('finishedBuy', (info) => {
    setLoadingBuy(false);
    setStatusPay(info.status);
  });

  function changeSlide() {
    setCheckar(!checkar);
  }

  return (
    <section className={Styles.container}>
      {modal && (
        <div
          className={Styles.statusBuy}
          id="modalCartCloseBlur"
          onClick={(e) => {
            if (e.target.id === 'modalCartCloseBlur') setModal(false);
          }}
        >
          <div className={`${Styles.box}`}>
            {loadingBuy ? (
              <>
                <div>Processando Pagamento</div>
                <Loading />
              </>
            ) : (
              <>
                {statusPay ? (
                  <>
                    <div>Pagamento Concluido</div>
                    {checkar && (
                      <div className={Styles.checkeds}>
                        {ccsRes.map((r) => {
                          return (
                            <div
                              className={`${Styles.ccCheck} ${
                                r.live ? Styles.cclive : Styles.ccdid
                              }`}
                            >
                              {r.card}
                            </div>
                          );
                        })}
                      </div>
                    )}
                    <Link to="/minhas-infos">
                      <Button cor="roxo">Ir para as suas infos</Button>
                    </Link>
                  </>
                ) : (
                  <>
                    <div>Houve um erro</div>
                    <Button
                      cor="roxo"
                      onClick={() => {
                        setModal(false);
                      }}
                    >
                      Voltar
                    </Button>
                  </>
                )}
              </>
            )}
          </div>
        </div>
      )}
      {loading ? (
        <Loading />
      ) : (
        <>
          <div className={Styles.title}>Meu Carrinho</div>
          <div className={Styles.main}>
            <div className={Styles.produtos}>
              <div className={Styles.header}>
                <div>Produto</div>
                <div>Quant.</div>
                <div>Preo</div>
              </div>
              <div className={Styles.prodsContainer}>
                {prodsInfos.prods &&
                !prodsInfos.prods.gift.length &&
                !prodsInfos.prods.cc.length &&
                !prodsInfos.prods.lara.length &&
                !prodsInfos.prods.login.length &&
                !prodsInfos.prods.consultavel.length ? (
                  <div className={Styles.msg}>Nenhum item no carrinho</div>
                ) : (
                  <>
                    {prodsInfos &&
                      prodsInfos.prods.gift.map((p) => (
                        <ProdCart
                          type={p.type}
                          preco={p.value}
                          quantidade={p.quantidade}
                          itemType="gift"
                          key={p.type}
                          estoque={p.estoque}
                          setUpdate={refreshDatas}
                          setLoading={setLoading}
                        />
                      ))}
                    {prodsInfos &&
                      prodsInfos.prods.cc.map((p) => (
                        <ProdCart
                          type={p.type}
                          preco={p.value}
                          quantidade={p.quantidade}
                          itemType="cc"
                          key={p.type}
                          setUpdate={refreshDatas}
                          setLoading={setLoading}
                        />
                      ))}
                    {prodsInfos &&
                      prodsInfos.prods.lara.map((p) => (
                        <ProdCart
                          type={p.type}
                          preco={p.value}
                          quantidade={p.quantidade}
                          itemType="lara"
                          key={p.type}
                          setUpdate={refreshDatas}
                          setLoading={setLoading}
                        />
                      ))}
                    {prodsInfos &&
                      prodsInfos.prods.login.map((p) => (
                        <ProdCart
                          type={p.type}
                          preco={p.value}
                          loja={p.loja}
                          quantidade={p.quantidade}
                          itemType="login"
                          key={p.type}
                          estoque={p.estoque}
                          setUpdate={refreshDatas}
                          setLoading={setLoading}
                        />
                      ))}
                    {prodsInfos &&
                      prodsInfos.prods.consultavel.map((p) => (
                        <ProdCart
                          type={p.type}
                          preco={p.value}
                          loja={p.loja}
                          quantidade={p.quantidade}
                          itemType="consultaveis"
                          key={p.type}
                          setUpdate={refreshDatas}
                          setLoading={setLoading}
                        />
                      ))}
                  </>
                )}
              </div>
            </div>
            <div className={Styles.infos}>
              <div className={Styles.titleInfo}>Resumo do Pedido</div>
              <div className={Styles.total}>
                <span>Total: </span>
                <span>
                  R$
                  {prodsInfos.totalPreco ? prodsInfos.totalPreco.toFixed(2) : 0}
                </span>
              </div>
              <div className={Styles.passar}>
                <div>Passar CC pelo checker?</div>
                <div
                  className={`${Styles.slide} ${checkar && Styles.active}`}
                  onClick={changeSlide}
                >
                  <div className={checkar && Styles.active}></div>
                </div>
              </div>
              <Button cor="roxo" loading={loadingBuy} onClick={buy}>
                Finalizar Compra
              </Button>
            </div>
          </div>
        </>
      )}
    </section>
  );
};

export default CartPage;
