import React from 'react';
import { Route, Routes } from 'react-router-dom';
import Header from '../Partials/Header';
import Main from './Main';
import MainArea from './components/MainArea';
import Regras from './pages/Regras';
import LojaRouter from './Loja/LojaRouter';
import CartPage from '../Cart/CartPage';
import MinhasInfosRouters from './pages/MinhasInfos/MinhasInfosRouters';
import Perfil from './pages/Perfil/Perfil';
import Dashboard from './pages/Dashboard/Dashboard';

const MainRouters = () => {
  return (
    <>
      <Main>
        <Header />
        <MainArea>
          <Routes>
            <Route path="/minhas-infos/*" element={<MinhasInfosRouters />} />
            <Route path="/regras" element={<Regras />} />
            <Route path="/loja/*" element={<LojaRouter />} />
            <Route path="/carrinho" element={<CartPage />} />
            <Route path="/minha-conta/*" element={<Perfil />} />
            <Route path="/" element={<Dashboard />} />
          </Routes>
        </MainArea>
      </Main>
    </>
  );
};

export default MainRouters;
