import React from "react";
import { Route, Routes } from "react-router";
import CcInfoPage from "./pages/cc/CcInfoPage";
import CcBonusInfoPage from "./pages/ccbonus/CcBonusInfoPage";
import ConsultavelInfoPage from "./pages/consultavel/ConsultavelInfoPage";
import GiftInfoPage from "./pages/gift/GiftInfoPage";
import LaraInfoPage from "./pages/lara/LaraInfoPage";
import LoginPageInfo from "./pages/login/LoginPageInfo";

const MinhasInfosRouters = () => {
  return (
    <Routes>
      <Route path="cc" element={<CcInfoPage />} />
      <Route path="bonuscc" element={<CcBonusInfoPage />} />
      <Route path="gift" element={<GiftInfoPage />} />
      <Route path="consultavel" element={<ConsultavelInfoPage />} />
      <Route path="lara" element={<LaraInfoPage />} />
      <Route path="login" element={<LoginPageInfo />} />
    </Routes>
  );
};

export default MinhasInfosRouters;
